	module.exports = {"data":{"post":{"html":"<p>I'll let others comment on the treatment of the detainees held by the US in Cuba. <a href=\"http://www.spiegel.de/politik/ausland/0,1518,178536,00.html\">Spiegel Online</a> has an interesting article on the American reaction to the conditions in Cuba (see headline) and to the international criticism. It concludes (my translation): \"The USA doesn't exactly suffer from modesty. It never tires of proclaiming itself to be world's best democracy,  with the world's finest constitution. It shouldn't be surprised when it is measured against its own words.\"</p>","fields":{"title":"They're Getting What They Deserve","date":"23 Jan 2002","url":"https://papascott.de/2002-01-23-theyre-getting-what-they-deserve/"}}},"pathContext":{"slug":"/2002-01-23-theyre-getting-what-they-deserve/","prev":{"fields":{"title":"NPD Ban Backfires","date":"23 Jan 2002","slug":"/2002-01-23-npd-ban-backfires/"}},"next":{"fields":{"title":"Unenemployed, And Doing Nothing About It","date":"23 Jan 2002","slug":"/2002-01-23-unenemployed-and-doing-nothing-about-it/"}}}}

